body {
  padding: 0;
  margin: 0 0;
  font-family: 'Fahkwang', sans-serif;
}
.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  .itemLeft {
    width: 15%;
  }
  
  .itemCenter {
      width: 70%;
  }
  
  .itemRight {
    width: 15%;
  }
  
  .navbar {
    background: black;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }
  
  li {
    color: white;
    padding: 15px 0;
  }
  
  body {
    font-size: 15px;
  }
.header{
	background-image: url(/clickmemorygame/static/media/banner.c1795043.jpg);
	position: relative;
	width: 100%;
}

.bgimage{
	object-fit: cover;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 80px 0 20px 0;
	color: #b30000; 
}

h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 40px;
	color: #b30000;
	font-weight: bold;
}
.cards {
    width: 200px;
    height: 200px;
    margin: 15px 15px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }

.img-container {
  width: 200px;
  height: 200px;
}

.image{
  width: 195px;
  height: 195px;
  object-fit: cover;
}
.footer{
	background-color: black;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
}
.wrapper {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 15px 0 15px;
  justify-content: space-around;
  align-content: flex-start;
  background-color: antiquewhite;
}
